<template>
  <div class="main">
    <div class="card" v-for="(item, index) in list" :key="index">
      <div class="card__top">
        <div class="card__top__line">
          <span class="card__top__icon iconfont">&#xe8ba;</span>
          <span style="color: rgb(71, 113, 186)">红山区图书馆</span>
        </div>
        <div class="card__top__line">
          <span class="card__top__icon iconfont">&#xe8bc;</span>
          <span>*春旭</span>
        </div>
        <div class="card__top__line">
          <span class="card__top__icon iconfont">&#xe8ae;</span>
          <span>150***********6115</span>
        </div>
        <div class="card__top__line">
          <span class="card__top__icon iconfont">&#xe8b9;</span>
          <span>133****9297</span>
        </div>
        <div class="card__top__line">
          <span class="card__top__icon iconfont">&#xe8b8;</span>
          <span>{{item}}</span>
        </div>
      </div>
      <div class="card__bottom">
        <button>取消预约</button>
        <div style="margin-left: 30px">
          我的二维码
          <span class="iconfont">&#xe685;</span>
        </div>
      </div>
    </div>

    <div class="tap" @click="handleClick"> </div>

    <div v-if="show" class="config">
      <select v-model="index">
        <option value="0">1</option>
        <option value="1">2</option>
        <option value="2">3</option>
        <option value="3">4</option>
      </select>      
      <input v-model="time" type="text" placeholder="显示的时间字符串">
      <button @click="handleSubmit">修改</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: true,
      time: '',
      index: 0,
      list: [
        "2021-07-14 08:30-11:30", "2021-07-14 08:30-11:30", "2021-07-14 08:30-11:30", "2021-07-14 08:30-11:30"
      ]
    };
  },
  mounted() {
    try {
      this.list = JSON.parse(localStorage.list)      
    } catch {
      this.list = [
        "2021-07-14 08:30-11:30", "2021-07-14 08:30-11:30", "2021-07-14 08:30-11:30", "2021-07-14 08:30-11:30"
      ]
    }
  },
  methods: {
    handleSubmit() {
      this.list[this.index] = this.time
      localStorage.list = JSON.stringify(this.list)
    },
    handleClick() {
      this.show = !this.show
    }
  }
};
</script>

